@import "../../assets/styles/app";

.charlieToken {
  padding: 52px 24px 30px;

  @include desktop {
    padding: 110px 0 620px;
  }

  .inner {

    h1.title {
      @include Redaction;
      color: #F7F8F8;
      text-align: center;
      font-size: 96px;
      line-height: 110%;

      @include desktop {
        font-size: 128px;
      }
    }

    .buttons {
      @include onlyMobile(flex);
      flex-direction: column;
      margin-top: 270px;

      .manifesto {
        @include button;
        margin-top: 30px;
        color: #F7F8F8;
        font-size: 14px;
        line-height: 130%;
      }
    }

  }
}
