@import "../../assets/styles/app";

.about {
  position: relative;

  .gradient_top {
    @include onlyDesktop;
    position: absolute;
    z-index: 1;
    top: 0;
    transform: translateY(-50%);
    left: 0;
    height: 639px;
    width: 100%;
  }

  .gradient_bottom {
    @include onlyDesktop;
    position: absolute;
    z-index: 1;
    bottom: 0;
    transform: translateY(50%);
    left: 0;
    height: 639px;
    width: 100%;
  }

  @include desktop {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .top {
    position: relative;
    z-index: 2;
    padding-top: 120px;

    @include desktop {
      padding-top: 100px;
      padding-bottom: 100px;
    }

    .about_mobile_top {
      @include onlyMobile;
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      width: 100%;
      height: 327px;
    }

    .swiper {
      position: relative;
      z-index: 2;
      width: 100%;
      @include onlyMobile(flex);

      .slide {
        width: 100%;
        box-sizing: border-box;
        padding: 0 24px;

        .label {
          display: flex;
          align-items: center;

          span {
            @include Redaction;
            color: #F7F8F8;
            font-size: 22px;
            line-height: 130%;
          }

          .disclaimer {
            @include button;
            width: 25px;
            height: 25px;
            margin-left: 14px;

            img {
              display: block;
              width: 25px;
              height: 25px;
            }
          }
        }

        .texts {
          margin-top: 20px;

          & > p {
            color: #F7F8F8;
            font-size: 16px;
            line-height: 130%;
            letter-spacing: -0.32px;

            &:not(:first-child) {
              margin-top: 10px;
            }
          }
        }

      }
    }

    .items {
      @include onlyDesktop;
      width: 584px;
      margin-left: auto;

      .item {

        &:not(:first-child) {
          margin-top: 100px;
        }

        .label {
          display: flex;
          align-items: center;

          span {
            @include Redaction;
            color: #F7F8F8;
            font-size: 40px;
            line-height: 130%;
          }

          .disclaimer {
            @include onlyDesktop;
            @include button;
            width: 25px;
            height: 25px;
            margin-left: 20px;

            img {
              display: block;
              width: 25px;
              height: 25px;
            }
          }
        }

        .texts {
          margin-top: 50px;
          width: 448px;

          & > p {
            color: #F7F8F8;
            font-size: 16px;
            line-height: 130%;
            letter-spacing: -0.32px;

            &:not(:first-child) {
              margin-top: 30px;
            }
          }
        }
      }

    }

  }

  .bottom {
    position: relative;
    background: #f0f1ec;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 1050px;
    box-sizing: border-box;
    overflow-x: hidden;

    @include desktop {
      height: auto;
    }

    .gradient {
      @include onlyMobile;
      position: absolute;
      z-index: 1;
      left: 0;
      top: -1px;
      width: 100%;
      height: 330px;
      transform: rotate(180deg);
    }

    .left1 {
      position: absolute;
      z-index: 2;
      width: 96px;
      height: auto;
      left: 0;
      top: 350px;

      @include desktop {
        width: 196px;
        top: 200px;
      }
    }

    .right1 {
      position: absolute;
      z-index: 2;
      width: 10px;
      height: auto;
      right: 50px;
      top: 350px;

      @include desktop {
        width: 14px;
        top: 350px;
        right: 70px;
      }
    }

    .right2 {
      position: absolute;
      z-index: 2;
      width: 10px;
      height: auto;
      right: 25px;
      top: 550px;

      @include desktop {
        width: 14px;
        top: 650px;
        right: 30px;
      }
    }

    .heads {
      position: absolute;
      z-index: 2;
      top: 450px;
      left: 20%;
      right: 15%;
      bottom: 0px;
      //background: rgba(0, 255, 0, 0.2);
      overflow: hidden;

      @include desktop {
        top: 450px;
        left: 25%;
        right: 10%;
        bottom: 300px;
      }

      &::before {
        position: absolute;
        z-index: 3;
        content: "";
        display: block;
        left: 0;
        top: 0;
        right: 0;
        height: 80px;
        background: linear-gradient(rgba(240, 241, 236, 1), rgba(240, 241, 236, 0));
      }

      &::after {
        position: absolute;
        z-index: 3;
        content: "";
        display: block;
        left: 0;
        bottom: 0;
        right: 0;
        height: 80px;
        background: linear-gradient(rgba(240, 241, 236, 0), rgba(240, 241, 236, 1));
      }

      .heads_inner {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        //border: 2px solid #000;

        svg {
          position: absolute;
          width: 50px;
          height: 50px;

          @include desktop {
            width: 82px;
            height: 82px;
          }

          &:nth-child(1) {
            left: 20%;
            top: 10%;
          }

          &:nth-child(2) {
            right: 10%;
            top: 25%;
          }

          &:nth-child(3) {
            left: 25%;
            top: 40%;
          }

          &:nth-child(4) {
            right: 15%;
            top: 55%;
          }

          &:nth-child(5) {
            left: 15%;
            top: 65%;
          }

          &:nth-child(6) {
            right: 40%;
            top: 85%;
          }
        }
      }
    }
  }
}

.tooltipContent {
  padding: 20px;
  background: #000;
  max-width: none!important;
  border-right: 2px solid #282828;

  &_label {
    @include Redaction;
    color: #F7F8F8;
    font-size: 28px;
    line-height: 130%;
  }

  &_text {
    color: #F7F8F8;
    font-size: 16px;
    line-height: 120%;
    letter-spacing: -0.32px;
    margin-top: 20px;
    width: 420px;
  }

}
