@import "../../assets/styles/app";

.howToBuy {
  background-color: #F0F1EC;
  padding-top: 50px;
  padding-bottom: 100px;

  @include desktop {
    padding-top: 150px;
    padding-bottom: 100px;
  }

  h2.title {
    @include h2;
    @include h2_dark;
    position: relative;
    z-index: 2;
  }

  .top {
    position: relative;
    z-index: 2;
    margin-top: 30px;
    padding: 0 24px;

    @include desktop {
      margin-top: 50px;
      padding: 0;
    }


    .inner {
      position: relative;

      @include desktop {
        width: 970px;
        margin-left: auto;
        margin-right: auto;
      }

      .btn {
        @include onlyDesktop;
        @include button;
        position: absolute;
        top: 68px;
        width: 60px;
        height: 60px;


        &_left {
          left: 0;

          .arrow {
            transform: translate(-50%, -50%) rotate(180deg);
          }
        }

        &_right {
          right: 0;

          .arrow {
            transform: translate(-50%, -50%) rotate(0deg);
          }
        }

        .back {
          display: block;
          width: 60px;
          height: 60px;
        }

        .arrow {
          position: absolute;
          top: 50%;
          left: 50%;
          width: 15px;
          height: 25px;
          //transform: translate(-50%, -50%);
        }
      }

      .text {
        @include Redaction;
        color: #030303;
        font-size: 22px;
        line-height: 130%;

        @include desktop {
          text-align: center;
          width: 772px;
          margin-left: auto;
          margin-right: auto;
        }
      }

      .prerequisites {
        margin-top: 20px;
        color: #030303;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: -0.32px;

        span {
          font-weight: bold;
        }


        @include desktop {
          text-align: center;
          width: 570px;
          margin-left: auto;
          margin-right: auto;

        }
      }

    }
  }

  .sliderMobile {
    @include onlyMobile;
    position: relative;
    z-index: 2;
    margin-top: 50px;
    padding-left: 21px;
    padding-right: 21px;

    .slide {
      position: relative;
      width: 270px;

      .line {
        position: absolute;
        left: 40px;
        top: 13px;
        height: 14px;
        width: 230px;
      }


      .step {
        position: relative;
        width: 40px;
        height: 40px;

        .back {
          display: block;
          width: 40px;
          height: 40px;
        }

        p {
          @include Redaction;
          @include absoluteCenter;
          color: #F7F8F8;
          font-size: 28px;
          line-height: 130%;
        }

      }

      .label {
        @include Redaction;
        color: #030303;
        font-size: 22px;
        line-height: 130%;
        margin-top: 7px;
      }

      .text {
        color: #030303;
        font-size: 12px;
        line-height: 120%;
        letter-spacing: -0.24px;
        width: 252px;
        overflow-wrap: break-word;
      }
    }


  }

  .sliderDesktop {
    @include onlyDesktop(grid);
    position: relative;
    //grid-template-columns: repeat(3, 1fr);
    //grid-column-gap: 70px;
    z-index: 2;
    margin-top: 75px;
    width: 970px;
    margin-left: auto;
    margin-right: auto;

    //.line {
    //  position: absolute;
    //  z-index: 1;
    //  left: 0;
    //  top: 13px;
    //  width: 969px;
    //  height: auto;
    //}

    .item {
      position: relative;
      z-index: 2;
      width: 323px;

      .line {
        position: absolute;
        left: 40px;
        top: 11px;
        height: auto;
        width: 283px;
      }

      .step {
        position: relative;
        width: 40px;
        height: 40px;

        .back {
          display: block;
          width: 40px;
          height: 40px;
        }

        p {
          @include Redaction;
          @include absoluteCenter;
          color: #F7F8F8;
          font-size: 28px;
          line-height: 130%;
        }

      }

      .label {
        @include Redaction;
        color: #030303;
        font-size: 22px;
        line-height: 130%;
        margin-top: 10px;
      }

      .text {
        color: #030303;
        font-size: 12px;
        line-height: 120%;
        letter-spacing: -0.24px;
        margin-top: 5px;
        width: 277px;
      }

    }

  }


}
